<template>
    <div class="login-wrap">
        <div class="login-info">
            <div class="logo">LOGO</div>
            <p class="slogan">一号房产网络经纪人平台系统</p>
            <div class="login">

                <p class="login-title">登录</p>
                 
                  <el-form ref="loginform" :model="form" :rules="rules" label-width="100px">
                    <el-form-item label="手机号" prop="phone">
                        <el-input v-model.number.trim="form.phone" style="width: 300px;"></el-input>
                    </el-form-item> 
                        
                    <el-form-item label="短信验证码" prop="code" >
                        <el-input v-model="form.code" style="width: 180px;"></el-input>
                         <el-button type="primary" class="getcode" @click="sendMessage(1)" :disabled="disabled" style="width: 120px">{{ text }}</el-button>
                        </el-form-item>

                    </el-form-item>

                    <el-form-item label-width="0">
                        <el-button type="primary" class="submit" @click="login">登 录</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <footer class="login-foot">
            <p>Power By 南京禾利昌网络科技有限公司  Copyright © 2018-2020 All right Reserved</p>
        </footer>

    </div>
</template>

<script>
import { login, sendMessage } from "@/api/login";
import mixin from "../../mixin/index";
export default {
  mixins: [mixin],
  data() {
    var checkphone = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("手机号不能为空"));
      } else if (!/^1[0-9]{10}$/.test(this.form.phone)) {
        callback(new Error("手机号格式不正确"));
      } else {
        callback();
      }
    };
    var checkCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("验证码不能为空"));
      } else {
        callback();
      }
    };
    return {
      form: {
        code: "",
        phone: ""
      },
      rules: {
        phone: [{ validator: checkphone, trigger: "blur" }],
        code: [{ validator: checkCode, trigger: "blur" }]
      }
    };
  },
  methods: {
    login() {
      let data = { phone: this.form.phone, code: this.form.code };
      this.$store
        .dispatch("LoginByPhone", data)
        .then(res => {
          if (res.data.code == 1) {
            this.$router.push({ name: "home" });
          } else {
            this.$message.error("登录失败");
          }
        })
        .catch(() => { 
        });
    }
  }
};
</script>

<style lang='scss' scoped>
.login-wrap {
  width: 100%;
  height: 100%;
  background-image: url("~img/bg2.png");
}
.logo,
.slogan {
  text-align: center;
}
.slogan {
  height: 25px;
  font-size: 24px;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  line-height: 26px;
  margin: 30px 0;
}
.login-info {
  position: relative;
  height: 100%;
  width: 100%;
  padding-top: 100px;
}
.login {
  position: relative;
  margin: 0 auto;
  padding: 20px;
  width: 500px;
  height: 350px;
  color: #5c5d5d;
  border-radius: 10px;
  box-shadow: 5px 0px 20px rgba(92, 93, 93, 0.75);
  background: rgba(255, 255, 255, 1);
  opacity: 0.8;
}
.login-title {
  width: 48px;
  height: 23px;
  font-size: 24px;
  font-weight: 400;
  color: rgba(67, 67, 67, 1);
  line-height: 26px;
  margin-bottom: 40px;
}
.login-foot {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  line-height: 50px;
  background: rgba(0, 0, 0, 1);
  opacity: 0.5;
  color: #fff;
  text-align: center;
}
.submit {
  position: relative;
  left: 50%;
  top: 30px;
  transform: translateX(-50%);
}
</style>



